*,*::before,*::after{box-sizing:border-box}body{font-family:Roboto}a{text-decoration:none}ul{list-style:none}body,h1,h2,h3,h4,h5,p,ul,figure{margin:0px;padding:0px}ul,menu{margin:0;list-style:none}.container{margin:0 auto;width:100vw}@media screen and (min-width: 480px){.container{width:480px}}@media screen and (min-width: 768px){.container{width:768px}}@media screen and (min-width: 1200px){.container{width:1200px}}.header{padding-top:48px;background-image:url(../src/images/mobile/hero.jpeg);background-repeat:no-repeat;background-position:center center;width:100%;height:1104px;margin:0 auto}.header__top{display:flex;justify-content:center}.top__wrap{display:flex;flex-direction:column;align-items:flex-start}.nav__svg{fill:#fff}.top__nav{display:flex;align-items:center}.top__title{width:314px;color:#fff;font-family:Roboto;font-size:59px;font-weight:900;line-height:1;text-transform:uppercase}.top__button{color:rgba(255,255,255,.8);font-family:"DM Sans";font-size:18px;font-weight:700;line-height:150%;padding:11px;border-radius:108px;background:var(--Main-Red, #dd2242);width:315px;height:59px;text-align:center;border:none}@media screen and (min-width: 480px){.header{width:375px}}